/* -------------------------------------------------------
   Global Base Styles
--------------------------------------------------------*/
:root {
  --bg: #000;
  --bg-panel: #181818;
  --bg-panel-alt: #202020;
  --accent: #d62626;
  --accent-hover: #bc1f1f;
  --accent-focus: #ff3030;
  --green: #128c28;
  --green-hover: #0f7321;
  --text: #f5f5f5;
  --text-dim: #cfcfcf;
  --text-faint: #9a9a9a;
  --border: #333;
  --border-strong: #444;
  --radius-sm: 4px;
  --radius: 10px;
  --radius-lg: 18px;
  --transition: 160ms ease;
  --focus: 0 0 0 3px rgba(214,38,38,0.4);
  font-size: 16px;
}

* { box-sizing: border-box; }

html, body, #root {
  height: 100%;
  margin: 0;
  background: var(--bg);
  color: var(--text);
  -webkit-font-smoothing: antialiased;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Fira Sans", "Droid Sans", "Helvetica Neue", Arial, sans-serif;
}

body {
  background:
    radial-gradient(circle at 50% 25%, rgba(255,255,255,0.06), rgba(0,0,0,0) 70%),
    var(--bg);
  background-attachment: fixed;
}

/* Background watermark container; image injected via inline style */
.app-bg {
  position: fixed;
  inset: 0;
  pointer-events: none;
  background-repeat: no-repeat;
  background-position: center top 70px;
  background-size: 680px auto;
  opacity: 0.08;
  z-index: 0;
}
@media (max-width: 780px) {
  .app-bg {
    background-size: 480px auto;
    background-position: center top 40px;
    opacity: .10;
  }
}
@media (max-width: 520px) {
  .app-bg {
    background-size: 390px auto;
    background-position: center top 50px;
  }
}

/* Main layout container */
.app-shell {
  position: relative;
  z-index: 1;
  min-height: 100%;
  max-width: 950px;
  margin: 0 auto;
  padding: clamp(.75rem, 1.7vh, 1.2rem) clamp(.75rem, 2vw, 1.25rem) 4rem;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

h1 {
  margin: .5rem 0 .35rem;
  font-size: clamp(2.1rem, 6.6vw, 3.35rem);
  line-height: 1.05;
  font-weight: 700;
  letter-spacing: .5px;
  text-shadow: 0 0 10px rgba(0,0,0,.55);
}

h2 {
  margin: 0 0 .75rem;
  font-size: clamp(1.35rem, 3.5vw, 1.75rem);
  font-weight: 600;
  letter-spacing: .5px;
}

p { margin: 0 0 .75rem; line-height: 1.4; }

.panel {
  background: linear-gradient(145deg, var(--bg-panel), var(--bg-panel-alt));
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: clamp(1rem, 2.2vw, 1.85rem);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(4px);
  box-shadow: 0 4px 18px -6px rgba(0,0,0,.65);
}

.panel.compact { padding: 1rem 1.15rem; }

.panel-outline { border: 1px solid var(--border-strong); }

.divider {
  height: 1px;
  background: linear-gradient(to right, transparent, #444, transparent);
  margin: 1.25rem 0 1.1rem;
}

/* Leader banner */
.leader-banner {
  align-self: center;
  background: #1f1f1f;
  border: 1px solid #313131;
  padding: .55rem 1rem .6rem;
  border-radius: 12px;
  font-size: .95rem;
  font-weight: 500;
  display: flex;
  gap: .55rem;
  align-items: center;
  box-shadow: 0 0 0 1px #272727, 0 2px 8px -3px rgba(0,0,0,.4);
  max-width: 680px;
  width: 100%;
  justify-content: center;
  text-align: center;
}

.meta {
  font-size: .8rem;
  color: var(--text-faint);
  letter-spacing: .25px;
  margin-top: .6rem;
}

/* Auth box */
.auth-box {
  width: 100%;
  max-width: 520px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: .9rem;
}

/* Inputs */
input[type="text"],
input[type="email"],
input[type="password"] {
  width: 100%;
  background: #121212;
  border: 1px solid #2f2f2f;
  color: var(--text);
  padding: .85rem .9rem;
  border-radius: var(--radius);
  font-size: .93rem;
  transition: border-color var(--transition), background var(--transition);
  outline: none;
}
input:focus {
  border-color: var(--accent);
  background: #171717;
  box-shadow: var(--focus);
}
input::placeholder { color: #666; }

/* Buttons */
button {
  font-family: inherit;
  cursor: pointer;
  border: none;
  font-size: .95rem;
  font-weight: 600;
  letter-spacing: .3px;
  border-radius: var(--radius);
  padding: .85rem 1.2rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: .5rem;
  background: #2d2d2d;
  color: var(--text);
  transition: background var(--transition), transform 120ms ease;
  user-select: none;
}

button:hover { background: #373737; }
button:active { transform: translateY(2px); }
button:focus-visible { outline: none; box-shadow: var(--focus); }

button.primary { background: var(--accent); color: #fff; }
button.primary:hover { background: var(--accent-hover); }

button.green { background: var(--green); color: #fff; }
button.green:hover { background: var(--green-hover); }

button.danger { background: var(--accent); color: #fff; }
button.danger:hover { background: var(--accent-hover); }

button.outline {
  background: transparent;
  border: 1px solid var(--border-strong);
}
button.outline:hover { border-color: #555; background: #1a1a1a; }

button.small {
  font-size: .78rem;
  padding: .45rem .7rem;
  border-radius: 999px;
  font-weight: 500;
}
button.tiny {
  font-size: .7rem;
  padding: .35rem .55rem;
  border-radius: 999px;
  font-weight: 500;
}
button.full { width: 100%; }

button:disabled,
button.disabled {
  opacity: .55;
  cursor: not-allowed;
  filter: grayscale(.25);
}

button.linklike {
  background: none;
  border: none;
  padding: 0;
  color: var(--accent);
  font-weight: 600;
  font-size: .9rem;
  text-decoration: underline;
}
button.linklike:hover { color: var(--accent-hover); }

.scan-button.locked {
  opacity: .55;
  position: relative;
}
.scan-button.locked::after {
  content: "🔒";
  position: absolute;
  right: .75rem;
  font-size: 1rem;
  filter: drop-shadow(0 0 4px rgba(0,0,0,.6));
}

/* Auth toggle line */
.auth-toggle {
  text-align: center;
  font-size: .85rem;
  margin-top: .25rem;
  color: var(--text-faint);
}

/* Coupons */
.coupons {
  list-style: none;
  margin: 1rem 0 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: .6rem;
}

.coupon-item {
  background: #111;
  border: 1px solid #232323;
  padding: .65rem .85rem;
  border-radius: var(--radius);
  font-size: .9rem;
  display: flex;
  align-items: center;
  gap: .65rem;
  position: relative;
  min-height: 42px;
}
.coupon-item.redeemed { opacity: .65; }

.coupon-status {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: .45rem;
  font-weight: 500;
}

.badge {
  display: inline-flex;
  padding: .25rem .55rem;
  border-radius: 999px;
  font-size: .65rem;
  background: #2e2e2e;
  color: #ddd;
  letter-spacing: .5px;
  text-transform: uppercase;
  font-weight: 600;
}
.badge.success { background: var(--green); color: #fff; }
.badge.dim { background: #3a3a3a; color: var(--text-faint); }

/* Admin */
.admin-list {
  display: flex;
  flex-direction: column;
  gap: 1.1rem;
  margin-top: .75rem;
}
.admin-user {
  background: #151515;
  border: 1px solid #292929;
  padding: 1rem 1rem 1.05rem;
  border-radius: var(--radius);
  display: flex;
  flex-direction: column;
  gap: .75rem;
  position: relative;
}
.admin-user-header {
  display: flex;
  flex-wrap: wrap;
  gap: .55rem 1rem;
  align-items: baseline;
  font-size: .93rem;
}
.admin-actions {
  display: flex;
  flex-wrap: wrap;
  gap: .6rem;
}

/* Modal / QR Scanner */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,.72);
  backdrop-filter: blur(3px);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: clamp(.5rem, 4vh, 2.5rem) clamp(.6rem, 3vw, 1.75rem);
  z-index: 50;
  overflow-y: auto;
}

.modal {
  width: 100%;
  max-width: 560px;
  background: linear-gradient(160deg, #181818, #141414);
  border: 1px solid #2c2c2c;
  border-radius: 24px;
  padding: clamp(1.2rem, 2.5vw, 2rem) clamp(1.1rem, 2vw, 1.8rem) clamp(1.3rem, 2.5vw, 2rem);
  box-shadow: 0 12px 40px -14px rgba(0,0,0,.8), 0 2px 8px -3px rgba(0,0,0,.55);
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.modal h2 {
  text-align: center;
  margin: .25rem 0 .4rem;
  font-size: clamp(1.55rem, 4vw, 1.95rem);
  letter-spacing: .75px;
}
.subtitle {
  margin: 0 auto .75rem;
  text-align: center;
  font-size: .9rem;
  max-width: 430px;
  color: var(--text-dim);
  line-height: 1.4;
}

#qr-scanner-container {
  width: 100%;
  aspect-ratio: 1/1;
  position: relative;
  background: #000;
  border: 2px solid var(--accent);
  border-radius: 22px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
#qr-scanner-container video {
  width: 100% !important;
  height: 100% !important;
  object-fit: cover;
}
.scan-frame {
  position: absolute;
  inset: 0;
  border: 3px solid rgba(214,38,38,0.65);
  border-radius: 22px;
  pointer-events: none;
  box-shadow: 0 0 0 999px rgba(0,0,0,.15) inset;
  animation: pulseBorder 2.5s ease-in-out infinite;
}
@keyframes pulseBorder {
  0%,100% { box-shadow: 0 0 0 999px rgba(0,0,0,.15) inset; }
  50% { box-shadow: 0 0 0 999px rgba(0,0,0,.28) inset; }
}

.locked-msg {
  text-align: center;
  padding: 1rem;
  font-size: .9rem;
  line-height: 1.4;
}

.scan-status {
  font-size: .85rem;
  color: var(--text-faint);
  text-align: center;
  min-height: 1.2rem;
}
.scan-status.error { color: #ff6262; }

.modal-footer {
  display: flex;
  justify-content: center;
  margin-top: .5rem;
}
.close-modal {
  background: #3a3a3a;
  color: #f3f3f3;
  min-width: 160px;
}
.close-modal:hover { background: #454545; }

/* Animations */
.fade-in { animation: fadeIn .4s ease; }
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(4px); }
  to { opacity: 1; transform: translateY(0); }
}
.panel-animate { animation: raise .5s ease; }
@keyframes raise {
  0% { opacity: 0; transform: translateY(14px); }
  100% { opacity: 1; transform: translateY(0); }
}

/* Badges */
.badge.success { background: var(--green); color:#fff; }
.badge.dim { background: #3a3a3a; color: var(--text-faint); }

/* Utilities */
.full { width: 100%; }
.muted { color: var(--text-faint); }
.hidden { display: none !important; }

/* Accessibility */
.visually-hidden {
  position:absolute!important;
  width:1px; height:1px;
  padding:0; margin:-1px;
  overflow:hidden;
  clip:rect(0 0 0 0);
  white-space:nowrap;
  border:0;
}

/* High Contrast (Windows) */
@media (forced-colors: active) {
  button:focus-visible,
  input:focus-visible {
    outline: 2px solid Highlight;
    outline-offset: 2px;
  }
}
.scanner-stage {
  position: relative;
  width: 100%;
  aspect-ratio: 1/1;
  background: #000;
  border: 2px solid var(--accent);
  border-radius: 22px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.scanner-stage video {
  width: 100% !important;
  height: 100% !important;
  object-fit: cover;
  background: #000;
  display: block;
}
.pre-scan, .loading-msg {
  text-align: center;
  font-size: .9rem;
  color: var(--text-dim);
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  gap: .9rem;
  align-items: center;
}
.scan-hint {
  position: absolute;
  left: 50%; top: 50%;
  transform: translate(-50%, -50%);
  font-size: .95rem;
  font-weight: 600;
  letter-spacing: .5px;
  color: #e0e0e0;
  text-shadow: 0 2px 6px rgba(0,0,0,.6);
}
.scan-frame.success {
  border-color: #18c346;
  animation: none;
  box-shadow: 0 0 0 999px rgba(0,0,0,.25) inset;
}
